/*-------------------------------------

 Button normal state

-------------------------------------*/
.button a,
a.button {
  background: #64BAFF;
  text-align: center;
  color: #fff;
  display: block;
  text-decoration: none;
  text-shadow: none;
  text-transform: uppercase;
  border-radius: 4px;
  -webkit-transition: all 0.3s ease-in;
  -moz-transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in;
  padding: 15px 25px;
  line-height: 1;
  border: 2px solid #64BAFF;
  font-weight: bold;
  position: relative;
  letter-spacing: 0.1em;
  position: relative;
}

@media (min-width: 0em) and (max-width: 40em) {
  .button a,
a.button {
    display: block;
  }
}
@media (min-width: 40.063em) {
  .button a,
a.button {
    display: inline-block;
  }
}
.button a:hover,
a.button:hover {
  background: #fff;
  color: #64BAFF;
  border-color: #fff;
  text-decoration: none;
}

@media (min-width: 64.063em) {
  .button a:before,
a.button:before {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid #64BAFF;
    display: block;
    content: "";
    transition: all 0.3s ease-out;
  }

  .button a:hover:before,
a.button:hover:before {
    opacity: 0;
    -webkit-transform: scale(1.2, 1.4);
    transform: scale(1.2, 1.4);
    transition: all 0.3s ease-out;
  }
}
