.l-video-wrapper {
  transition: top 0.3s linear 0s;
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  z-index: 0;
}

@media (min-width: 0em) and (max-width: 40em) {
  .l-video-wrapper {
    display: block;
    padding-bottom: 100vh;
  }
}
@media (min-width: 40.063em) and (max-width: 64em) {
  .l-video-wrapper {
    display: block;
    padding-bottom: 100vh;
  }
}
@media (min-width: 0em) {
  .l-video-wrapper {
    display: block;
    padding-bottom: 100vh;
  }
}
@media (min-width: 90.063em) {
  .l-video-wrapper {
    display: block;
    padding-bottom: 100vh;
  }
}
video {
  position: absolute;
  top: 0%;
  left: 0%;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

#overlay {
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(15, 37, 46, 0.7);
}

.l-mobile-splash,
.l-tablet-splash {
  position: relative;
  z-index: 1;
}

.l-mobile-splash:after,
.l-tablet-splash:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
  background-color: rgba(28, 46, 50, 0.65);
  z-index: 2;
}