//------------------------------------------------------
//
//	MIXINS - BUTTONS - define groups of attributes for buttons
//
//	location: 
//      /sass/abstractions/mixins-buttons.scss
//
//------------------------------------------------------



// button mixins

@mixin button {
	background: $button-bg-color; 
	text-align: center;
	color: $button-text-color;
	display: block;
	text-decoration: none;
	text-shadow: none;
	text-transform: $button-text-transform;
	@include border-radius($button-border-radius);
	@include transition(all, 0.3s, ease-in);
	@include button-padding($button-padding);
	line-height: 1;
	border: $button-border-width solid $button-border-color;
	font-weight: $button-font-weight;
	position: relative;
	letter-spacing: 0.1em;
	
	@include breakpoint($small-only) {
		display: block;
	}
	@include breakpoint($medium-up) {
		display: inline-block;
	}
}

@mixin button-hover {
	background: $button-bg-hover-color; 
	color: $button-text-hover-color;
	border-color: $button-border-hover-color;
	text-decoration: none;
}

@mixin button-padding($button-padding) {
	padding: $button-padding;
}

// button mixin LIGHT BUTTON ON DARK BG
